import React, { useEffect, useState } from "react";
import "./services.css";
import $ from "jquery";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import ReactOwlCarousel from "react-owl-carousel";
import { motion } from "framer-motion";
import Transition from "../Transition/Transition";
import { Helmet } from "react-helmet";

export default function Services() {
  let [items, setItems] = useState();
  let media = window.matchMedia("(max-width: 991px)");
  media.addEventListener("change", ({ media, matches }) => {
    if (matches === true) {
      setItems(1);
    } else {
      setItems(3);
    }
  });
  function isMatches() {
    if (media.matches === true) {
      setItems(1);
    } else {
      setItems(3);
    }
  }
  useEffect(() => {
    isMatches();
  }, []);

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <link
          rel="icon"
          type="image/x-icon"
          href="/src/assets/images/logo-1.png"
        />
        <title>Services</title>
      </Helmet>
      <Transition />
      <div className="services">
        <div className="myContainer">
          <div className="title">
            <motion.h2
              initial={{ x: "-1000px" }}
              animate={{ x: "0px", transition: { duration: 0.7, delay: 2.4 } }}
              className="mainTitle"
            >
              My Services
              <span> .</span>
            </motion.h2>
            <motion.p
              initial={{ y: "-1000px" }}
              animate={{
                y: "0px",
                transition: { duration: 0.7, delay: 2.4, type: "spring" },
              }}
            >
              Here's the details of what I can give you.
            </motion.p>
          </div>
          <div className="info w-100">
            <ReactOwlCarousel
              className="owl-theme w-100 "
              id="owel"
              items={items}
            >
              <motion.div
                initial={{ y: "500px" }}
                animate={{
                  y: "0px",
                  transition: { duration: 1, delay: 2, type: "spring" },
                }}
                className="item card"
              >
                <i class="fa-solid fa-copyright"></i>
                <h4>Branding</h4>
                <p>Lorem ipsum dolor sit, amet consectetur</p>
                <i class="fa-solid fa-arrow-right myArrow"></i>
              </motion.div>
              <motion.div
                initial={{ y: "500px" }}
                animate={{
                  y: "0px",
                  transition: { duration: 1, delay: 2.2, type: "spring" },
                }}
                className="item card"
              >
                <i class="fa-solid fa-code"></i>
                <h4>Development</h4>
                <p>Lorem ipsum dolor sit, amet consectetur</p>
                <i class="fa-solid fa-arrow-right myArrow"></i>
              </motion.div>
              <motion.div
                initial={{ y: "500px" }}
                animate={{
                  y: "0px",
                  transition: { duration: 1, delay: 2.4, type: "spring" },
                }}
                className="item card"
              >
                <i class="fa-solid fa-server"></i>
                <h4>SEO</h4>
                <p>Lorem ipsum dolor sit, amet consectetur</p>
                <i class="fa-solid fa-arrow-right myArrow"></i>
              </motion.div>
              <motion.div
                initial={{ y: "500px" }}
                animate={{
                  y: "0px",
                  transition: { duration: 1, delay: 2.4, type: "spring" },
                }}
                className="item card"
              >
                <i class="fa-solid fa-pen-nib"></i>
                <h4>Design</h4>
                <p>Lorem ipsum dolor sit, amet consectetur</p>
                <i class="fa-solid fa-arrow-right myArrow"></i>
              </motion.div>
              <motion.div
                initial={{ y: "500px" }}
                animate={{
                  y: "0px",
                  transition: { duration: 1, delay: 2.4, type: "spring" },
                }}
                className="item card"
              >
                <i class="fa-solid fa-chart-simple"></i>
                <h4>Markting</h4>
                <p>Lorem ipsum dolor sit, amet consectetur</p>
                <i class="fa-solid fa-arrow-right myArrow"></i>
              </motion.div>
              <motion.div
                initial={{ y: "500px" }}
                animate={{
                  y: "0px",
                  transition: { duration: 1, delay: 2.4, type: "spring" },
                }}
                className="item card"
              >
                <i class="fa-solid fa-keyboard"></i>
                <h4>Typeing</h4>
                <p>Lorem ipsum dolor sit, amet consectetur</p>
                <i class="fa-solid fa-arrow-right myArrow"></i>
              </motion.div>
            </ReactOwlCarousel>
          </div>
        </div>
      </div>
    </>
  );
}
