import { useEffect, useMemo, useState } from "react";

import { motion } from "framer-motion";
import "./homeStyle.css";
import spiner from "../../assets/images/spin.png";
import avatar_1 from "../../assets/images/avatar-1.png";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";
import Poo from "../Poo";


export default function Home() {
  useEffect(() => {
    getKey();
  }, []);

  function getKey() {
    let key = document.querySelectorAll(".test");
    for (let i = 0; i < key.length; i++) {
      if (key[i].getAttribute("key") > 23) {
        key[i].style.cssText = " color: white;";
      }
    }
  }

  let text = "Transforming ideas into Digital reality ";

  const h3Variants = {
    hidden: {
      opacity: 0,
    },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.05,
      },
    },
  };
  const spanVariants = {
    hidden: {
      opacity: 0,
    },
    visible: {
      opacity: 1,
    },
  };

  return (
    <>
      
      <Helmet>
        <meta charSet="utf-8" />
        <link
          rel="icon"
          type="image/x-icon"
          href="/src/assets/images/logo-1.png"
        />
        <title>Wagih world</title>
      </Helmet>
      <div className="myContainer ">
        <div className="home ">
          <div className="about">
            <motion.h3
              variants={h3Variants}
              initial="hidden"
              animate="visible"
              className="main"
            >
              {text.split("").map((char, index) => {
                return (
                  <>
                    <motion.span
                      className={`${index > 23 ? "textColor" : ""}`}
                      variants={spanVariants}
                      key={index}
                    >
                      {char}
                    </motion.span>
                  </>
                );
              })}
            </motion.h3>
            <motion.p
              initial={{ y: "500px" }}
              animate={{ y: "0px" }}
              transition={{
                duration: 1,
                delay: 0.5,
                type: "spring",
              }}
            >
              Hi there, I'm Ahmed Wagih, a Front-End Developer with a passion
              for React and Next.js. I have extensive experience building
              high-performance, user-friendly web applications. I'm also skilled
              in UI/UX design and have a keen eye for detail.
            </motion.p>
            <motion.div
              initial={{ x: "-1000px" }}
              animate={{ x: "0px" }}
              transition={{
                duration: 2,
                delay: 1,
                type: "spring",
                mass: 0.3,
                damping: 8,
              }}
              className=" position-relative d-flex justify-content-center align-items-center"
            >
              <Link to="/work">
                <div className="circle">
                  <img src={spiner} className="w-100 spin " alt="" />
                  <i class="fa-solid fa-arrow-right"></i>
                </div>
              </Link>
            </motion.div>
          </div>
          <motion.div
            initial={{ y: "1000px" }}
            animate={{ y: "0px" }}
            transition={{
              duration: 0.6,
              delay: 1,
            }}
            className="picture"
          >
            <picture>
              <img src={avatar_1} alt="" />
            </picture>
          </motion.div>
        </div>
      </div>
    </>
  );
}
