import { motion } from "framer-motion";
import "./work.css";
import React from "react";
import ReactOwlCarousel from "react-owl-carousel";
import project from "../../assets/images/work-1.jpg";
import project_2 from "../../assets/images/work-3.jpg";
import project_3 from "../../assets/images/work-4.jpg";
import project_4 from "../../assets/images/work-6.jpg";
import project_5 from "../../assets/images/work-7.jpg";
import project_6 from "../../assets/images/work-8.jpeg";
import project_7 from "../../assets/images/work-9.jpeg";
import Transition from "../Transition/Transition";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";

export default function Work() {
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <link
          rel="icon"
          type="image/x-icon"
          href="/src/assets/images/logo-1.png"
        />
        <title>Work</title>
      </Helmet>
      <Transition />
      <div className="work">
        <div className="myContainer">
          <div className="title">
            <motion.h2
              initial={{ x: "-1000px" }}
              animate={{ x: "0px", transition: { duration: 0.7, delay: 2.4 } }}
              className="mainTitle"
            >
              My work
              <span> .</span>
            </motion.h2>
            <motion.p
              initial={{ y: "-1000px" }}
              animate={{
                y: "0px",
                transition: { duration: 0.7, delay: 2.4, type: "spring" },
              }}
            >
              These are in the form of my project, which has different ideas and
              uses different technologies in each of them. Click on it to visit
              the site.
            </motion.p>
          </div>
          <div className="info w-100">
            <ReactOwlCarousel
              className="owl-theme  wol_1"
              dots={false}
              items={2}
              margin={7}
            >
              <Link to="https://wagih-13.github.io/e-commerce/" target="_blank">
                <motion.div
                  initial={{ x: "-500px" }}
                  animate={{
                    x: "0px",
                    transition: { duration: 1, delay: 2, type: "spring" },
                  }}
                  className="card "
                  projectName="E-commerce"
                >
                  <img src={project} alt="" className="w-100" />
                </motion.div>
              </Link>
              <Link to="https://wagih-13.github.io/Dashboard/" target="_blank">
                <motion.div
                  initial={{ y: "-500px" }}
                  animate={{
                    y: "0px",
                    transition: { duration: 1, delay: 2, type: "spring" },
                  }}
                  className="card"
                  projectName="Dash board"
                >
                  <img src={project_5} alt="" className="w-100" />
                </motion.div>
              </Link>
              <Link to="https://wagih-13.github.io/Meals/" target="_blank">
                <div className="card " projectName="Meals">
                  <img src={project_3} alt="" className="w-100" />
                </div>
              </Link>
              <Link to="https://wagih-13.github.io/Kasper/" target="_blank">
                <div className="card " projectName="Kasper">
                  <img src={project_6} alt="" className="w-100" />
                </div>
              </Link>
            </ReactOwlCarousel>
            <ReactOwlCarousel className="owl-theme" items={2} margin={7}>
              <Link to="https://wagih-13.github.io/Wagih/" target="_blank">
                <motion.div
                  initial={{ y: "500px" }}
                  animate={{
                    y: "0px",
                    transition: { duration: 1, delay: 2, type: "spring" },
                  }}
                  className="card "
                  projectName="Portfolio"
                >
                  <img src={project_4} alt="" className="w-100" />
                </motion.div>
              </Link>
              <Link to="https://weather-lyart-six.vercel.app/" target="_blank">
                <motion.div
                  initial={{ x: "500px" }}
                  animate={{
                    x: "0px",
                    transition: { duration: 1, delay: 2, type: "spring" },
                  }}
                  className="card"
                  projectName="Weather"
                >
                  <img src={project_2} alt="" className="w-100" />
                </motion.div>
              </Link>
              <Link to="https://wagih-13.github.io/Bookmarke/" target="_blank">
                <div className="card " projectName="Book mark">
                  <img src={project_7} alt="" className="w-100" />
                </div>
              </Link>
              <Link to="https://wagih-13.github.io/e-commerce/" target="_blank">
                <div className="card " projectName="E-commerce">
                  <img src={project} alt="" className="w-100" />
                </div>
              </Link>
            </ReactOwlCarousel>
          </div>
        </div>
      </div>
    </>
  );
}
