import React, { useEffect } from "react";
import "./about.css";
import Transition from "../Transition/Transition";
import { useState } from "react";
import { motion } from "framer-motion";
import CountUp from "react-countup";
import { Helmet } from "react-helmet";

export default function About() {
  let [index, setIndex] = useState(0);
  let AboutData = [
    {
      title: "Skills",
      disc: (
        <>
          <div className="disc1">
            <span>Web Development :</span>
            <span className="icons">
              <i class="fa-brands fa-html5"></i>
              <i class="fa-brands fa-css3-alt"></i>
              <i class="fa-brands fa-js"></i>
              <i class="fa-brands fa-react"></i>
              <i class="fa-brands fa-bootstrap"></i>
              <i class="fa-brands fa-sass"></i>
            </span>
            <span>
              , redux , context , typeScript , reactQurey , jquery ,ecmascript 6
            </span>
          </div>
          <div className="disc2">
            <span>Ui/Ux :</span>
            <span className="icons">
              <i class="fa-brands fa-figma"></i>
            </span>
          </div>
        </>
      ),
    },
    {
      title: "softSkills",
      disc: (
        <>
          <div className="AwardDisc">
            <span>
              - Ability to receive observations and integrate them into action.
              <br />
              - The ability to explain the technical concepts of non-programmers
              clearly and effectively. <br /> - For his ability to work
              cooperatively with other members of the Panel.
            </span>
          </div>
        </>
      ),
    },
    {
      title: "Experience",

      disc: (
        <>
          <div className="exDisc">
            <span>
              I work as a freelancer about a 1 year ago and I've faced many
              challenges and problems, and I've learned a lot of them.
            </span>
          </div>
        </>
      ),
    },
    {
      title: "Credentials",
      disc: (
        <>
          <div className="credDisc">
            <span>- programming fundamentals diploma with c++ . </span>
            <span className="year">2022 - 2023</span>
          </div>
          <div className="credDisc">
            <span>- User interface programming diploma . </span>
            <span className="year">2023 - 2024</span>,
          </div>
        </>
      ),
    },
  ];
  function ChangeIndex(indx) {
    setIndex(indx);
    let header = document.querySelectorAll(".header");
    let headerAfter = window.getComputedStyle(header[indx], "::after");
    header[indx].style.setProperty("--width", "100%");
    header[indx].style.setProperty("--backColor", "#ff0000");
    for (let i = 0; i < 4; i++) {
      if (i !== indx) {
        let headerAfter = window.getComputedStyle(header[i], "::after");
        header[i].style.setProperty("--width", "40%");
        header[i].style.setProperty("--backColor", "white");
      }
    }
  }

  useEffect(() => {
    let header = document.querySelectorAll(".header");
    header[0].style.setProperty("--width", "100%");
    header[0].style.setProperty("--backColor", "#ff0000");
  }, []);

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <link
          rel="icon"
          type="image/x-icon"
          href="/src/assets/images/logo-1.png"
        />
        <title>About</title>
      </Helmet>
      <Transition />
      <div className="about">
        <div className=" myContainer">
          <div className="info">
            <motion.h3
              initial={{ x: "-1000px" }}
              animate={{ x: "0px", transition: { duration: 1, delay: 1.6 } }}
              className="mainTitle"
            >
              Captivating <span>stories </span>birth magnificent designs.
            </motion.h3>
            <motion.p
              initial={{ y: "-1000px" }}
              animate={{
                y: "0px",
                transition: { duration: 0.7, delay: 1.6, type: "spring" },
              }}
            >
              I have experience dealing with all kinds of API summonses and
              using Redux and Context, and I have good experience in React and
              Next. I use these skills to apply the appropriate technology to
              each project according to its needs.
            </motion.p>
            <motion.div
              initial={{ y: "1000px" }}
              animate={{
                y: "0px",
                transition: {
                  duration: 1,
                  delay: 2,
                  type: "spring",
                },
              }}
              className="counter"
            >
              <div className="content">
                <span className="number">
                  <CountUp start={0} end={1} duration={3} delay={2.5} />+
                </span>
                <span>YEARS OF EXPERIENCE</span>
              </div>
              <div className="content">
                <span className="number">
                  <CountUp start={0} end={5} duration={3} delay={2.5} />+
                </span>
                <span>SATISFIED CLIENTS</span>
              </div>
              <div className="content">
                <span className="number">
                  <CountUp start={0} end={30} duration={3} delay={2.5} />+
                </span>
                <span>FINISHED PROJECTS</span>
              </div>
              <div className="content">
                <span className="number">
                  <CountUp start={0} end={1} duration={3} delay={2.5} />
                </span>
                <span>WINNING AWARDS</span>
              </div>
            </motion.div>
          </div>
          <motion.div
            initial={{ scale: 0 }}
            animate={{
              scale: 1,
              transition: {
                duration: 1,
                delay: 2,
                type: "spring",
                mass: 0.3,
                damping: 5,
              },
            }}
            className="right-side"
          >
            <div className="skils-title">
              {AboutData.map((element, elementIndex) => {
                return (
                  <>
                    <h6
                      className={`${
                        elementIndex === index ? " textMain" : ""
                      } mb-4 header`}
                      key={elementIndex}
                      onClick={(eI) => {
                        ChangeIndex(elementIndex);
                      }}
                    >
                      {element.title}
                    </h6>
                  </>
                );
              })}
            </div>
            <div className="skils-disc">{AboutData[index].disc}</div>
          </motion.div>
        </div>
      </div>
    </>
  );
}

