import React from "react";
import "./contact.css";
import { useFormik } from "formik";
import { object, string, number, date, InferType } from "yup";
import { motion } from "framer-motion";
import Transition from "../Transition/Transition";
import { Helmet } from "react-helmet";

export default function Contact() {
  function formPostData(val) {
    console.log(val);
  }

  function inputCheck() {
    let firstNameInput = document.getElementById("firstName");
    let lastNameInput = document.getElementById("lastName");
    let emailInput = document.getElementById("email");
    if (myForm.errors.firstName && myForm.touched.firstName) {
      firstNameInput.value = "";
      firstNameInput.classList.add("tests");
    } else {
      firstNameInput.classList.remove("tests");
    }
    if (myForm.errors.lastName && myForm.touched.lastName) {
      lastNameInput.value = "";
      lastNameInput.classList.add("tests");
    } else {
      lastNameInput.classList.remove("tests");
    }
    if (myForm.errors.email && myForm.touched.email) {
      emailInput.value = "";
      emailInput.classList.add("tests");
    } else {
      emailInput.classList.remove("tests");
    }
  }

  let validationSchema = object({
    firstName: string().required("name is requird ").min(3),
    lastName: string().required("last name is requird ").min(3),
    phone: number().required("phone is requird "),
    email: string()
      .required("email is required")
      .email("enter valid name")
      .matches(
        /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
        "email must be be"
      ),
  });
  let myForm = useFormik({
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      message: "",
      phone: 0,
    },
    onSubmit: formPostData,
    validationSchema,
  });

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <link
          rel="icon"
          type="image/x-icon"
          href="/src/assets/images/logo-1.png"
        />
        <title>Contact us</title>
      </Helmet>
      <Transition />
      <div className="contact">
        <div className="myContainer">
          <motion.div
            className="mainTitle"
            initial={{ y: "-1000px" }}
            animate={{
              y: "0px",
              transition: { delay: 2, duration: 2, type: "spring" },
            }}
          >
            <h2>
              Let's <span>Contact .</span>
            </h2>
          </motion.div>

          <motion.form
            onSubmit={myForm.handleSubmit}
            className="contactForm"
            initial={{ y: "1000px" }}
            animate={{
              y: "0px",
              transition: { delay: 2, duration: 2, type: "spring" },
            }}
          >
            <motion.input
              initial={{ y: "1000px" }}
              animate={{
                y: "0px",
                transition: { delay: 2, duration: 2, type: "spring" },
              }}
              type="text "
              id="firstName"
              name="firstName"
              placeholder="First name"
              onChange={myForm.handleChange}
              onBlur={myForm.handleBlur}
            />
            <motion.input
              initial={{ y: "1000px" }}
              animate={{
                y: "0px",
                transition: { delay: 2.1, duration: 2, type: "spring" },
              }}
              type="text"
              name="lastName"
              id="lastName"
              placeholder="Last name"
              onChange={myForm.handleChange}
              onBlur={myForm.handleBlur}
            />
            <motion.input
              initial={{ y: "1000px" }}
              animate={{
                y: "0px",
                transition: { delay: 2.2, duration: 2, type: "spring" },
              }}
              type="email"
              name="email"
              id="email"
              placeholder="Email"
              onChange={myForm.handleChange}
              onBlur={myForm.handleBlur}
            />
            <motion.input
              initial={{ y: "1000px" }}
              animate={{
                y: "0px",
                transition: { delay: 2, duration: 2, type: "spring" },
              }}
              type="tel "
              name="phone"
              placeholder="phone"
              onChange={myForm.handleChange}
              onBlur={myForm.handleBlur}
              isError="false"
            />
            <motion.textarea
              initial={{ y: "1000px" }}
              animate={{
                y: "0px",
                transition: { delay: 2.3, duration: 1.5, type: "spring" },
              }}
              name="message"
              placeholder="write message"
              onChange={myForm.handleChange}
              onBlur={myForm.handleBlur}
            ></motion.textarea>
            <button onClick={inputCheck}>Send</button>
          </motion.form>
        </div>
      </div>
    </>
  );
}
