import { motion } from "framer-motion";
import React from "react";
import "./transitionStyle.css";
export default function Transition() {
  const transitionVariants = {
    initial: {
      width: "0%",
    },
    animate: {
      width: ["0%", "100%", "100%", "100%", "0%"],
    },
    exit: {
      width: "100%",
      y: "-100vh",
    },
  };
  return (
    <div>
      <motion.div
        className="background-1"
        variants={transitionVariants}
        initial="initial"
        animate="animate"
        exit="exit"
        transition={{
          delay: 0,
          duration: 2,
          ease: "easeInOut",
        }}
      ></motion.div>
      <motion.div
        className="background-2"
        variants={transitionVariants}
        initial="initial"
        animate="animate"
        exit="exit"
        transition={{ delay: 0.2, duration: 2 }}
      ></motion.div>
      <motion.div
        className="background-3"
        variants={transitionVariants}
        initial="initial"
        animate="animate"
        exit="exit"
        transition={{ delay: 0.4, duration: 2 }}
      ></motion.div>
    </div>
  );
}
